import { NgModule,NO_ERRORS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { DirectGrnRoutingModule } from './direct-grn-routing.module';
import { DirectGrnComponent } from './direct-grn.component';
import { DirectGrnAddComponent } from './direct-grn-add/direct-grn-add.component';
import { DirectGrnEditComponent } from './direct-grn-edit/direct-grn-edit.component';
import { DirectGrnViewComponent } from './direct-grn-view/direct-grn-view.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    DirectGrnRoutingModule
  ],
  entryComponents:[
    DirectGrnAddComponent,
    DirectGrnEditComponent,
    DirectGrnViewComponent
  ],
  declarations: [
    DirectGrnAddComponent,
    DirectGrnEditComponent,
    DirectGrnComponent,
    DirectGrnViewComponent
  ]
})
export class DirectGrnModule { }
{ }



