import { Component, OnInit, Input, Inject, AfterViewInit, ChangeDetectorRef, Output, EventEmitter, ViewChild } from '@angular/core';
import { DirectGrnService } from '../../../../shared';
import { jqxGridComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxgrid';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';


@Component({
  selector: 'app-direct-grn-view',
  templateUrl: './direct-grn-view.component.html',
  styleUrls: ['./direct-grn-view.component.scss']
})
export class DirectGrnViewComponent implements OnInit {
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myGrid') myGrid: jqxGridComponent;
  @Input('dgvData') dgvData;

  
  source: any;
  dataAdapter: any;
  columns: any[]; 
  apiUrl: string;
  material_array: Array<any> = [];
  constructor(private cdr: ChangeDetectorRef,
    private dgs: DirectGrnService) { }
  
    ngOnInit() {
      this.getItem();
    }

    ngAfterViewInit() {
      this.jqxLoader.open();
      console.log(this.dgvData.id)
      this.dgs.show(this.dgvData.id).subscribe(
        result => { 
          this.material_array = result['data'][0].direct_g_r_n_items;
          console.log(result['data'][0]);
          for (var _i = 0; _i < this.material_array.length; _i++) {
            this.material_array[_i]['name'] = this.material_array[_i]['item'] && this.material_array[_i]['item'].name;
        }
          this.source.localdata = this.material_array;
          this.myGrid.updatebounddata("cells");
          this.jqxLoader.close();
        }, 
        error => { 
          console.log(error);
        }
      );
      this.cdr.detectChanges();
    }
  
    getItem(){
      this.source =
      {
          datatype: 'json',
          datafields:
  
          [
             
            { name: 'name', type: 'string' },
            { name: 'item_id', type: 'string' },
            { name: 'quantity', type: 'string' },
            { name: 'value', type: 'string' },
            { name: 'rate', type: 'string' },              
            { name: 'vat', type: 'string' },
             { name: 'total', type: 'string' },
             { name: 'remarks', type: 'string' },
          ],
          id: 'id',
          
      };
      this.dataAdapter = new jqx.dataAdapter(this.source);
  
      this.columns =
      [
        {
          text: 'S.N', editable: false,
          groupable: false, draggable: false, resizable: false,
          datafield: 'id', columntype: 'number', width: 50,
          cellsrenderer: function (row, column, value) {
              return "<div style='margin:4px;'>" + (value + 1) + "</div>";
          }
      },
        {
          text: 'Item Name', datafield: 'item_id', displayfield: 'name', columntype: 'combobox', width: 120,
          createeditor: (row: number, column: any, editor: any): void => {
            let contractorsource =
              {
                datatype: "json",
                contentType: "application/json; charset=utf-8",
                datafields: [
                  { name: 'id' },
                  { name: 'name' },
                ],
                async: false,
                url: this.apiUrl + 'item?limit=all'
              };
            let contractorAdapter = new jqx.dataAdapter(contractorsource, {
              downloadComplete: (data, status, xhr) => {
                let tData = data.data;
                return tData;
              }
            });
            editor.jqxComboBox({
              source: contractorAdapter,
              displayMember: "name",
              valueMember: "id"
            });
          }
        },
        { text: 'Quantity', columntype: 'textbox', datafield: 'quantity', width: 120 },
        { text: 'Rate', datafield: 'rate', columntype: 'textbox', width: 70 },
        { text: 'Value', datafield: 'value', columntype: 'textbox', width: 70 },
        { text: 'Vat', datafield: 'vat', columntype: 'textbox', width: 120 },
        {
          text: 'Total', datafield: 'total', columntype: 'textbox', width: 120, cellsrenderer: (index: number, datafield: string, value: any, defaultvalue: any, column: any, rowdata: any): string => {
            if (rowdata.quantity && rowdata.rate) {
              let total = parseFloat(rowdata.quantity) * parseFloat(rowdata.rate);
              return '<div style="jqx-align-right">' + this.dataAdapter.formatNumber(total, 'f2') + '</div>';
            }
          }
        },
        { text: 'Remarks', datafield: 'remarks', columntype: 'textbox' },
      
      ];
   
    }
    rendertoolbar = (toolbar: any): void => {
      let container = document.createElement('div');
      container.style.margin = '5px';
      let headingContainer4 = document.createElement('div');
    
      headingContainer4.id = 'headingContainer4';
    
      headingContainer4.style.cssText = 'float: left;padding:5px;margin-right:10px;font-weight:bold;';
    
      headingContainer4.innerHTML = ' Item Details:';
    
      container.appendChild(headingContainer4);
      toolbar[0].appendChild(container);
    
    };
  
}



