import { Component, OnInit, Input, Output, Inject, ChangeDetectorRef, EventEmitter, ViewChild } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { DirectGrnService, DirectGrn, MaterialIssueService,ErrorHandleService} from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { jqxDateTimeInputComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxdatetimeinput';
import { jqxGridComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxgrid';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';

@Component({
  selector: 'app-direct-grn-edit',
  templateUrl: './direct-grn-edit.component.html',
  styleUrls: ['./direct-grn-edit.component.scss']
})
export class DirectGrnEditComponent implements OnInit {
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;
  @ViewChild('datetime') datetime: jqxDateTimeInputComponent;
  @ViewChild('myGridAdd') myGridAdd: jqxGridComponent;
  @ViewChild('typeComboBox') typeComboBox: jqxComboBoxComponent;
  @Input('dgEditData') dgEditData;
  @Output() UpdatedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  dgForm: FormGroup;
  EditData: any;
  dgData: DirectGrn;
  material_issue: any;
  materialAdapter: any;
  apiUrl: string;
  source: any;
  dataAdapter: any;
  columns: any[];
  material_array: Array<any> = [];


  typeAdapter: any[] = [
    { value: 'HO', label: 'Head Office' },
    { value: 'SES', label: 'Sister Estate' },
    { value: 'RE', label: 'Return' },
    { value: 'AD', label: 'Adjustment' },
    { value: 'VEN', label: 'Vendor' },
  ];

  constructor(
    private fb: FormBuilder,
    private dgService: DirectGrnService,
    private cdr: ChangeDetectorRef,
    private ehs: ErrorHandleService,
    private miService: MaterialIssueService,
    @Inject('API_URL') apiUrl: string, ) {
    this.apiUrl = apiUrl;
    this.createForm();
  }

  ngOnInit() {
    this.dgData = new DirectGrn(this.dgEditData);
    this.dgForm.setValue(this.dgData);
  }
  createForm() {

    this.dgForm = this.fb.group({
      'code': [null, Validators.required],
      'direct_grn_no': [null, Validators.required],
      'remarks': [null],
      'cr_account_head_id': [null, Validators.required],
      'date': [null],
      // 'receipt_no': [null, Validators.required],
      'dr_account_head': [null, Validators.required],
      'dr_account_sub_head': [null, Validators.required],
      'cr_account_head': [null, Validators.required],
      'cr_account_sub_head': [null, Validators.required],
      'bill_code': [null, Validators.required],
      'bill_no': [null, Validators.required],
    });
  }
 
  saveBtn(post) {
    this.myValidator.validate(document.getElementById('dgForm'));
    post['type'] = this.typeComboBox.val();
    if (post['date'] && post['type']) {
      this.jqxLoader.open();
      this.dgService.update(this.dgEditData.id, post).subscribe(
        result => {
          let data = this.ehs.formatError(result);
          if (data['result'] == true) {
            this.UpdatedData.emit(data);
          } else {
            let messageDiv: any = document.getElementById('windowMessage');
            messageDiv.innerHTML = '';
            for (let i = 0; i < data['message'].length; i++) {
              let errorDiv = document.createElement('li');
              errorDiv.innerText = data['message'][i];
              messageDiv.appendChild(errorDiv);
            }
            this.winNotification.open();
          }
          this.jqxLoader.close();
        },
        error => {
          let data = this.ehs.formatError(error);
          let messageDiv: any = document.getElementById('windowMessage');
          messageDiv.innerText = data['message'];
          this.winNotification.open();
          this.jqxLoader.close();
        }
      );
    }
  }
  cancelBtn() {
    this.cancelData.emit(true);
  }
  rules =
    [
      { input: '.code', message: 'Code is required !', action: 'keydown, blur', rule: 'required' },
      { input: '.direct_grn_no', message: 'Direct grn no is required!', action: 'keydown, blur', rule: 'required' },
      { input: '.remarks', message: 'Remarks is required !', action: 'keydown, blur', rule: 'required' },
      { input: '.cr_account_head_id', message: 'Cr Account head id is required !', action: 'keydown, blur', rule: 'required' },
      // { input: '.receipt_no', message: 'Receipt no is required !', action: 'keydown, blur', rule: 'required' },
      { input: '.dr_account_head', message: 'Dr account head is required !', action: 'keydown, blur', rule: 'required' },
      { input: '.dr_account_sub_head', message: 'Dr account sub head is required !', action: 'keydown, blur', rule: 'required' },
      { input: '.cr_account_head', message: 'Cr account head is required !', action: 'keydown, blur', rule: 'required' },
      { input: '.cr_account_sub_head', message: 'Cr account sub head is required !', action: 'keydown, blur', rule: 'required' },
      { input: '.bill_code', message: 'Bill code is required !', action: 'keydown, blur', rule: 'required' },
      { input: '.bill_no', message: 'Bill no is required !', action: 'keydown, blur', rule: 'required' },
      {
        input: '.date', message: 'Date is required!', action: 'keydown, blur', rule: (input: any, commit: any): any => {
          let data = this.datetime.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      {
        input: '.type', message: 'Type is required !', action: 'keydown, blur,change', rule: (input: any, commit: any): any => {
          let data = this.typeComboBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
    ];
}



