import { Component, OnInit, Inject, Output, EventEmitter, ViewChild } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { DirectGrnService, MaterialIssueService, CurrentUserService,ErrorHandleService } from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { jqxDateTimeInputComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxdatetimeinput';
import { jqxGridComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxgrid';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';

@Component({
  selector: 'app-direct-grn-add',
  templateUrl: './direct-grn-add.component.html',
  styleUrls: ['./direct-grn-add.component.scss']
})
export class DirectGrnAddComponent implements OnInit {
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;
  @ViewChild('datetime') datetime: jqxDateTimeInputComponent;
  @ViewChild('myComboBox') myComboBox: jqxComboBoxComponent;
  @ViewChild('typeComboBox') typeComboBox: jqxComboBoxComponent;
  @ViewChild('directGrnAddGrid') directGrnAddGrid: jqxGridComponent;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  @Output() AddedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();
  dgForm: FormGroup;
  material_issue: any;
  materialAdapter: any;
  apiUrl: string;
  source: any;
  dataAdapter: any;
  columns: any[];
  material_array: Array<any> = [];
  material_id: number;

  grnType: boolean = false;
  materialIssue: boolean = false;

  typeAdapter: any[] = [
    { value: 'HO', label: 'Head Office' },
    { value: 'SES', label: 'Sister Estate' },
    { value: 'RE', label: 'Return' },
    { value: 'AD', label: 'Adjustment' },
    { value: 'VEN', label: 'Vendor' },
  ];

  userData: any = {};

  constructor(
    private fb: FormBuilder,
    private dgService: DirectGrnService,
    private ehs: ErrorHandleService,
    private miService: MaterialIssueService,
    private cus: CurrentUserService,
    @Inject('API_URL') apiUrl: string, ) {
    this.apiUrl = apiUrl;
    this.userData = this.cus.getTokenData()
    this.createForm();
  }

  ngOnInit() {
    this.getItem();
    this.material_issue =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'issue_no' },
        ],
        data: this.userData,
        async: false,
        url: this.apiUrl + 'material_issue?limit=all',
      };
    this.materialAdapter = new jqx.dataAdapter(this.material_issue, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }
    });
  }

  ngAfterViewInit(){
    this.datetime.value(new Date());
  }
  createForm() {

    this.dgForm = this.fb.group({
      'code': [null, Validators.required],
      'direct_grn_no': [null, Validators.required],
      'remarks': [null],
      'cr_account_head_id': [null, Validators.required],
      'date': [null],
      // 'receipt_no': [null, Validators.required],
      'dr_account_head': [null, Validators.required],
      'dr_account_sub_head': [null, Validators.required],
      'cr_account_head': [null, Validators.required],
      'cr_account_sub_head': [null, Validators.required],
      'bill_code': [null, Validators.required],
      'bill_no': [null, Validators.required],
    });
  }
  Change(event: any): void {
    this.materialIssue = this.myComboBox.val() ? true : false;
    this.miService.show(this.myComboBox.val()).subscribe(
      result => {
        this.material_array = result['data'].material_issue_details;

        for (var _i = 0; _i < this.material_array.length; _i++) {
          this.material_array[_i]['name'] = this.material_array[_i]['item'] && this.material_array[_i]['item'].name;
        }

        this.source.localdata = this.material_array;
        this.source.pagesize = this.material_array.length - 1;
        this.directGrnAddGrid.updatebounddata("cells");
        this.jqxLoader.close();

      },
      error => {
        console.log(error);
      }
    );
  }
  getItem() {
    this.source =
      {
        datatype: 'json',
        datafields:

          [
            { name: 'name', type: 'string' },
            { name: 'item_id', type: 'string' },
            { name: 'quantity', type: 'string' },
            { name: 'value', type: 'string' },
            { name: 'rate', type: 'string' },
            { name: 'vat', type: 'string' },
            { name: 'total', type: 'string' },
          ],
        id: 'id',

      };
    this.dataAdapter = new jqx.dataAdapter(this.source);

    this.columns =
      [
        {
          text: 'S.N', editable: false,
          groupable: false, draggable: false, resizable: false,
          datafield: 'id', columntype: 'number', width: 50,
          cellsrenderer: function (row, column, value) {
            return "<div style='margin:4px;'>" + (value + 1) + "</div>";
          }
        },
        {
          text: 'Item Name', datafield: 'item_id', displayfield: 'name', columntype: 'combobox', width: 120,
          createeditor: (row: number, column: any, editor: any): void => {
            let contractorsource =
              {
                datatype: "json",
                contentType: "application/json; charset=utf-8",
                datafields: [
                  { name: 'id' },
                  { name: 'name' },
                ],
                async: false,
                url: this.apiUrl + 'item?limit=all'
              };
            let contractorAdapter = new jqx.dataAdapter(contractorsource, {
              downloadComplete: (data, status, xhr) => {
                let tData = data.data;
                return tData;
              }
            });
            editor.jqxComboBox({
              source: contractorAdapter,
              displayMember: "name",
              valueMember: "id"
            });
          }
        },
        { text: 'Quantity', columntype: 'textbox', datafield: 'quantity', width: 120 },
        { text: 'Rate', datafield: 'rate', columntype: 'textbox', width: 70 },
        { text: 'Value', datafield: 'value', columntype: 'textbox', width: 70 },

        { text: 'Vat', datafield: 'vat', columntype: 'textbox', width: 120 },
        {
          text: 'Total', datafield: 'total', columntype: 'textbox', width: 120, cellsrenderer: (index: number, datafield: string, value: any, defaultvalue: any, column: any, rowdata: any): string => {
            if (rowdata.quantity && rowdata.rate) {
              let total = parseFloat(rowdata.quantity) * parseFloat(rowdata.rate);
              return '<div style="jqx-align-right">' + this.dataAdapter.formatNumber(total, 'f2') + '</div>';
            }
          }
        },

        {
          text: 'Actions', datafield: 'Delete', columntype: 'button',
          cellsrenderer: (): string => {
            return 'Delete';
          },
          buttonclick: (row: number): void => {
            let rowid = this.directGrnAddGrid.getrowid(row)
            this.directGrnAddGrid.deleterow(rowid);
          }
        }
      ];

  }
  rendertoolbar = (toolbar: any): void => {
    let container = document.createElement('div');
    container.style.margin = '5px';

    let headingContainer4 = document.createElement('div');
    let buttonContainer5 = document.createElement('div');

    headingContainer4.id = 'headingContainer4';
    buttonContainer5.id = 'buttonContainer5';

    headingContainer4.style.cssText = 'float: left;padding:5px;margin-right:10px;font-weight:bold;';
    buttonContainer5.style.cssText = 'float: right';

    headingContainer4.innerHTML = 'Material Issue Item Details:';

    container.appendChild(headingContainer4);
    container.appendChild(buttonContainer5);
    toolbar[0].appendChild(container);

  };

  generaterow(): any {
    let row = {};
    row['item_id'] = '';
    row['quantity'] = ''
    row['rate'] = '';
    row['vat'] = '';
    row['value'] = '';
    row['total'] = '';
    row['remarks'] = '';
    return row;
  }
  saveBtn(post) {
    this.myValidator.validate(document.getElementById('dgForm'));
    post['material_issue'] = this.myComboBox.val();
    post['directGRNItems'] = this.directGrnAddGrid.getrows();
    post['type'] = this.typeComboBox.val();
    if (post['date'] && post['material_issue'] && post['type']) {
      this.jqxLoader.open();
      this.dgService.store(post).subscribe(
        result => {
          let data = this.ehs.formatError(result);
          if (data['result'] == true) {
            this.AddedData.emit(data);
          } else {
            let messageDiv: any = document.getElementById('windowMessage');
            messageDiv.innerHTML = '';
            for (let i = 0; i < data['message'].length; i++) {
              let errorDiv = document.createElement('li');
              errorDiv.innerText = data['message'][i];
              messageDiv.appendChild(errorDiv);
            }
            this.winNotification.open();
          }
          this.jqxLoader.close();
        },
        error => {
          let data = this.ehs.formatError(error);
          let messageDiv: any = document.getElementById('windowMessage');
          messageDiv.innerText = data['message'];
          this.winNotification.open();
          this.jqxLoader.close();
        }
      );
    }

  }
  cancelBtn() {
    this.cancelData.emit(true);
  }


  rules =
    [
      { input: '.code', message: 'Code is required !', action: 'keydown, blur', rule: 'required' },
      { input: '.direct_grn_no', message: 'Direct grn no is required!', action: 'keydown, blur', rule: 'required' },
      { input: '.remarks', message: 'Remarks is required !', action: 'keydown, blur', rule: 'required' },
      { input: '.cr_account_head_id', message: 'Cr Account head id is required !', action: 'keydown, blur', rule: 'required' },
      // { input: '.receipt_no', message: 'Receipt no is required !', action: 'keydown, blur', rule: 'required' },
      { input: '.dr_account_head', message: 'Dr account head is required !', action: 'keydown, blur', rule: 'required' },
      { input: '.dr_account_sub_head', message: 'Dr account sub head is required !', action: 'keydown, blur', rule: 'required' },
      { input: '.cr_account_head', message: 'Cr account head is required !', action: 'keydown, blur', rule: 'required' },
      { input: '.cr_account_sub_head', message: 'Cr account sub head is required !', action: 'keydown, blur', rule: 'required' },
      { input: '.bill_code', message: 'Bill code is required !', action: 'keydown, blur', rule: 'required' },
      { input: '.bill_no', message: 'Bill no is required !', action: 'keydown, blur', rule: 'required' },
      {
        input: '.material_issue', message: 'Material name is required !', action: 'keydown, blur,change', rule: (input: any, commit: any): any => {
          let data = this.myComboBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      {
        input: '.type', message: 'Type is required !', action: 'keydown, blur,change', rule: (input: any, commit: any): any => {
          let data = this.typeComboBox.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
      {
        input: '.date', message: 'Date is required!', action: 'keydown, blur', rule: (input: any, commit: any): any => {
          let data = this.datetime.val();
          let result = (data != '') ? true : false;
          return result;
        }
      },
    ];

    grnTypeChanged() {
      this.grnType = this.typeComboBox.val() ? true : false;
    }

}



